// App-specific variables
// =========================
$celebrationAnniversaryFacts_yearTitleColor: $brand_color_darker !default;
$celebrationAnniversaryFacts_yearBgColor: $brand_color_darker !default;
$celebrationAnniversaryFacts_yearTextColor: $white !default;


// App-specific code
// ==================================================

/* ----- GLOBAL APP STYLES ----- */
// Note that every module and page and related instance of this app should be namespaced with the appName
.celebrationAnniversaryFacts {

} /* /.celebrationAnniversaryFacts  */

/* ----- MODULE STYLES ----- */
.module.celebrationAnniversaryFacts {

    .wide-view {
        padding: 0;
    }

    .carousel {

        .item {
            height: 321px;
        }

        .year-content {
            bottom: 0;
            position: absolute;
            width: 170px;

            .year {
                color: $celebrationAnniversaryFacts_yearTitleColor;
                font: 80px $celebration_secondaryFontFamily;//have to set this in PX so it matches up with the box below
                font-weight: $fontWeightExBold;
                letter-spacing: -5px;
                text-align: center;
                margin-left: -5px;
                position: relative;
                text-shadow: 0 0 12px rgba(0, 0, 0, 0.25);
                top: -4px;
                z-index: 1;
            }

            .year-desc {
                background-color: $celebrationAnniversaryFacts_yearBgColor;
                color: $celebrationAnniversaryFacts_yearTextColor;
                line-height: 1.2;
                margin: -30px 0 0 0;
                max-height: 190px;
                min-height: 64px;
                overflow: hidden;
                padding: 10px;
                position: relative;
                text-overflow: ellipsis;
                z-index: 2;
            }
        }
    }
} /* /.module.celebrationAnniversaryFacts  */

/* ----- PAGE STYLES ----- */
